.rol {
  height: 200px;
  width: 220px;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: black;
}

.rol span {
  height: 180px;
  width: 180px;
  border-radius: 50%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.rol span .icon {
  font-size: 90px;
}

.rol label {
  margin-top: 20px;
  font-weight: bold;
}
