.modal-registrar-entrada .modal-body {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 6px;
  padding: 0;
}

.modal-registrar-entrada .codigo {
  display: grid;
  grid-template-columns:  1fr 1fr 1fr;
  gap: 6px;
  padding: 0;
}
.modal-registrar-entrada .codigo .container {
  display: grid;
  grid-template-columns: 1fr;
  margin-bottom: 8px;
}
.modal-registrar-entrada .modal-body .container {
  display: grid;
  grid-template-columns: 1fr;
  margin-bottom: 8px;
}
.modal-registrar-entrada .modal-body .container_producto {
  display: grid;
  grid-template-columns: 70% 1fr;
  margin-bottom: 8px;
}

.modal-registrar-entrada .modal-body .agregar {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 15px;
}

.modal-registrar-entrada .tabla {
  margin-top: 20px;
}

.modal-registrar-entrada .tabla_salida {
  margin-top: 20px;
  width: 100%;
}

.modal-registrar-entrada .footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 10px;
}

.modal-registrar-entrada .footer button {
  border-radius: 20px;
  border: 1px solid #079434;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 110px;
}

.modal-registrar-entrada .footer button:hover {
  background-color: #079434;
  border: none;
  color: white;
}
