.modal-volquete .button-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.modal-volquete .button-container button {
  border-radius: 20px;
  border: 1px solid #079434;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 110px;
}

.modal-volquete .button-container button:hover {
  background-color: #079434;
  border: none;
  color: white;
}

.modal-volquete .modal-body .hide {
  display: none;
  visibility: hidden;
}
